// pages/index.js
'use client';          // ← tell Next.js this component runs in the browser
import { use, useState } from 'react';
import "../globals.css";

export default function ProfilePage() {
  const [preview, setPreview] = useState(false);
  const [profilePhoto, setProfilePhoto] = useState(null);
  const [profilePhotoURL, setProfilePhotoURL] = useState(null);
  const [name, setName] = useState('');
  const [title, setTitle] = useState('');
  const [about, setAbout] = useState('');
  const [experiences, setExperiences] = useState(['']);
  const [skillInput, setSkillInput] = useState('');
  const [skills, setSkills] = useState([]);
  const [portfolioFiles, setPortfolioFiles] = useState([]);

  const handleProfilePhotoChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setProfilePhoto(file);
      setProfilePhotoURL(URL.createObjectURL(file));
    }
  };

  const handleAddExperience = () => {
    setExperiences([...experiences, '']);
  };

  const handleExperienceChange = (idx, value) => {
    const list = [...experiences];
    list[idx] = value;
    setExperiences(list);
  };

  const handleRemoveExperience = (idx) => {
    setExperiences(experiences.filter((_, i) => i !== idx));
  };

  const handleAddSkill = () => {
    const skill = skillInput.trim();
    if (skill && !skills.includes(skill)) {
      setSkills([...skills, skill]);
      setSkillInput('');
    }
  };

  const handleRemoveSkill = (skill) => {
    setSkills(skills.filter(s => s !== skill));
  };

  const handlePortfolioChange = (e) => {
    setPortfolioFiles(Array.from(e.target.files));
  };

  return (
    <div className="min-h-screen bg-gray-50 text-gray-900 p-6">
      {!preview ? (
        <section className="max-w-4xl mx-auto space-y-6">
          <header className="flex justify-between items-center">
            <h1 className="text-2xl font-bold">Mon Profil Vendeur</h1>
            <button
              onClick={() => setPreview(true)}
              className="bg-blue-600 text-white px-4 py-2 rounded-md"
            >
              Aperçu
            </button>
          </header>

          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium mb-1">Photo de profil</label>
              <input
                type="file"
                accept="image/*"
                onChange={handleProfilePhotoChange}
                className="block w-full text-sm text-gray-500"
              />
              {profilePhotoURL && (
                <img
                  src={profilePhotoURL}
                  alt="Aperçu Photo"
                  className="mt-2 w-24 h-24 object-cover rounded-full"
                />
              )}
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">Nom complet</label>
              <input
                value={name}
                onChange={(e) => setName(e.target.value)}
                className="w-full border border-gray-300 rounded-md p-2"
                type="text"
              />
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">Titre professionnel</label>
              <input
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                className="w-full border border-gray-300 rounded-md p-2"
                type="text"
              />
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">À propos</label>
              <textarea
                value={about}
                onChange={(e) => setAbout(e.target.value)}
                className="w-full border border-gray-300 rounded-md p-2"
                rows={4}
              />
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">Expériences</label>
              {experiences.map((exp, idx) => (
                <div key={idx} className="flex items-start space-x-2 mb-2">
                  <textarea
                    value={exp}
                    onChange={(e) => handleExperienceChange(idx, e.target.value)}
                    className="flex-1 border border-gray-300 rounded-md p-2"
                    rows={2}
                  />
                  <button
                    onClick={() => handleRemoveExperience(idx)}
                    className="text-red-500"
                  >
                    ✕
                  </button>
                </div>
              ))}
              <button
                onClick={handleAddExperience}
                className="text-green-600 text-sm"
              >
                + Ajouter expérience
              </button>
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">Compétences</label>
              <div className="flex items-center space-x-2 mb-2">
                <input
                  value={skillInput}
                  onChange={(e) => setSkillInput(e.target.value)}
                  className="flex-1 border border-gray-300 rounded-md p-2"
                  type="text"
                />
                <button
                  onClick={handleAddSkill}
                  className="bg-green-600 text-white px-3 py-1 rounded-md"
                >
                  Ajouter
                </button>
              </div>
              <div className="flex flex-wrap gap-2">
                {skills.map((skill) => (
                  <span
                    key={skill}
                    className="bg-gray-200 px-3 py-1 rounded-full flex items-center space-x-1"
                  >
                    <span>{skill}</span>
                    <button onClick={() => handleRemoveSkill(skill)}>
                      ✕
                    </button>
                  </span>
                ))}
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">Portfolio</label>
              <input
                type="file"
                multiple
                onChange={handlePortfolioChange}
                className="block w-full text-sm text-gray-500"
              />
            </div>

            <button
              type="button"
              onClick={() => alert('Enregistré !')}
              className="bg-blue-600 text-white px-6 py-2 rounded-md"
            >
              Enregistrer
            </button>
          </div>
        </section>
      ) : (
        <section className="max-w-4xl mx-auto space-y-6">
          <header className="flex justify-between items-center">
            <h1 className="text-2xl font-bold">Aperçu Profil Public</h1>
            <button
              onClick={() => setPreview(false)}
              className="bg-gray-200 text-gray-800 px-4 py-2 rounded-md"
            >
              Modifier
            </button>
          </header>

          <div className="bg-white p-6 rounded-lg shadow-md">
            {profilePhotoURL && (
              <img
                src={profilePhotoURL}
                alt="Avatar"
                className="w-24 h-24 rounded-full object-cover mb-4"
              />
            )}
            {name && <h2 className="text-xl font-semibold mb-1">{name}</h2>}
            {title && <p className="text-gray-500 mb-4">{title}</p>}
            {about && (
              <div className="mb-4">
                <h3 className="font-medium mb-1">À propos</h3>
                <p>{about}</p>
              </div>
            )}
            {experiences.length > 0 && (
              <div className="mb-4">
                <h3 className="font-medium mb-1">Expériences</h3>
                <ul className="list-disc list-inside">
                  {experiences.map((exp, idx) => (
                    <li key={idx}>{exp}</li>
                  ))}
                </ul>
              </div>
            )}
            {skills.length > 0 && (
              <div className="mb-4">
                <h3 className="font-medium mb-1">Compétences</h3>
                <div className="flex flex-wrap gap-2">
                  {skills.map((skill) => (
                    <span key={skill} className="bg-gray-200 px-3 py-1 rounded-full">
                      {skill}
                    </span>
                  ))}
                </div>
              </div>
            )}
            {portfolioFiles.length > 0 && (
              <div>
                <h3 className="font-medium mb-1">Portfolio</h3>
                <div className="grid grid-cols-3 gap-4">
                  {portfolioFiles.map((file, idx) => (
                    <div key={idx} className="bg-gray-100 p-4 rounded-md">
                      <p className="text-sm truncate">{file.name}</p>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        </section>
      )}
    </div>
  );
}
